import 'antd/dist/antd.css';
import './index.css';
import React, { Component } from 'react'
import { Layout, Menu, Breadcrumb } from 'antd';
import Home from './home'
import Work from './work'
import Education from './education'
import Projetcs from './projects'
import Skills from './skils';

const { Header, Content, Footer } = Layout;




export default class App extends Component {

  handleChange(value)
  {
    console.log('alert aaya',value)
    switch (value) {
      case 1:
        this.setState(
          {
            content:<Home />
          }
        )
        break;
      case 2:
        this.setState(
          {
            content:<Work />
          }
        )
        break;
      case 3:
        this.setState(
          {
            content:<Education />   
          }
        )

        
        // expected output: "Mangoes and papayas are $2.79 a pound."
        break;
        case 4:
          this.setState(
            {
              content:<Projetcs />   
            }
          )
  
          
          // expected output: "Mangoes and papayas are $2.79 a pound."
          break;
          case 5:
            this.setState(
              {
                content:<Skills />   
              }
            )
    
            
            // expected output: "Mangoes and papayas are $2.79 a pound."
            break;
           
      default:
        this.setState(
          {
            content:<Home />
    
          }
        )
    }

  
  }

  constructor(props) {
    super(props)

    this.state = {
        home: "warning",
        results: "white",
        stats: "white",
        info: "white",
        content:<Home />

    }

    this.handleChange = this.handleChange.bind(this)
}




  render() {

  
    return (
      
            <Layout className="layout">
      <Header>
        <div className="logo" />
        <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['1']}>
          {["Home","Work Experience","Education","Projects","Skills"].map((value, index) => {
            const key = index + 1;
            return <Menu.Item key={key} onClick={()=>this.handleChange(key)}>{value} </Menu.Item>;
          })}
        </Menu>
      </Header>
      <Content style={{ padding: '0 50px' ,scroll :"y"}}>
        {this.state.content}
      </Content>
      <Footer style={{ textAlign: 'center' }}>Resume App</Footer>
    </Layout>

        
      
    )
  }
}

